*{box-sizing: border-box; margin:0; padding:0;}
*::before,*::after{box-sizing: border-box;}
.head{
  border: 7px solid black;
  border-top: 15px solid black;
  width: 200px;
  height: 200px;
  left: 50%;
  position: absolute;
  margin-left: -100px;
  border-radius: 50% 50% 60% 60%;
  top:50px;
  background: white;

}
.hair{
  border-color:red black green yellow ;
  width:0;
  height:0;
  border-right: 20px solid transparent;
  border-left: 20px solid transparent;
  border-top: 140px solid black;
  border-radius: 30% 30% 0 0;
  position: absolute;
  left: 50%;
  margin-left:-20px ;
  top: -40px;
}
.eye{
  border: 4px solid black;
  height: 70px;
  width: 70px;
  border-radius: 50% 80%;
  position: absolute;
  top:40px;
  background: #ffc30a;
}
@keyframes bling {
  0%{
    background:#ffc30a ;
  }
  50%{
    background: yellow;
  }
100%{
  background: #ffc30a;
}
}
.head:hover .eye{
  animation: bling 0.5s infinite ;
}
.eye.left{
  left:10px;
}
.eye.right{
  right:10px;
  transform: rotateY(180deg);
}
.mouth{
  border-top: 12px solid black;
  border-right: 5px solid transparent;
  border-left:5px solid transparent;
  width: 36px;
  height: 10px;
  position: absolute;
  left: 50%;
  top: 130px;
  margin-left: -18px;
}
.ear{
  border: 5px solid black;
  width: 30px;
  height: 60px;
  position: absolute;
  top:50px;
  left:50%;
  margin-left:-15px;
  z-index: -1;
}
.ear.left{
  transform: translateX(-98px) rotate(-8deg);
}
.ear.right{
  transform: translateX(98px) rotate(8deg);
}
.body{
  width: 180px;
  height: 280px;
  position: relative;
  top:180px;
  left: 50%;
 margin-left: -90px;
}
.pants{
  border: 7px solid black;
  width: 120px;
  height:90px;
  position: absolute;
  left:50%;
  margin-left: -60px;
  z-index: -1;
  border-radius: 0 0 10% 10%;
  background:#ff000e ;
  top:40px;
}

.shirt{
  border: 7px solid black;
  width: 120px;
  height:80px;
  position: absolute;
  top:30px;
  left:50%;
  margin-left: -60px;
  border-radius: 0 0 25% 25%;
  z-index: -1;
  background: white;
}
.tie{
  border: 3px solid black;
  position: absolute;
  width: 60px;
  height: 30px;
  left:50%;
  margin-left: -40px;
  top:50px;
  transform: rotate(45deg);
z-index: -1;
  background:#ff000e;
}
.tie::before{
  width:8px;
  height:8px;
  display:block;
  border:2px solid #000;
  border-radius:50%;
  background:#48e1e2;
  position:absolute;
  top:30px;
  left:25px;
  z-index:10;
}

.hand{
  border: 7px solid black;
  width: 40px;
  height: 115px;
  position: absolute;
  border-radius: 60% 60% 50% 50%;
  background: white;
}
.hand.left{
  left: 10px;
}

.hand.right{
  transform: rotate(-90deg);
  left:75px;
  z-index: 3;
}
.leg{
  border: 7px solid black;
  width: 50px;
  height: 120px;
  top: 70px;
  border-radius: 50% 50%;
  left: 50%;
  position: absolute;
  margin-left: -25px;
  z-index: -3;
}
.leg.left{
  transform: translateX(-35px) rotate(15deg);
}
.leg.right{
  transform: translateX(35px) rotate(-45deg);
}



